
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
.pic-adjust {
  overflow: hidden;
  width: 404px;
  margin: 0 auto;
}
.pic-adjust .wrap {
  position: relative;
  overflow: hidden;
  border: 1px solid #999;
  height: 202px;
}



